<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                    <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuUuidNumber">
               <el-input v-model="ruleForm.yonghuUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuName">
               <el-input v-model="ruleForm.yonghuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuPhone">
               <el-input v-model="ruleForm.yonghuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuIdNumber">
               <el-input v-model="ruleForm.yonghuIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="yonghuPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.yonghuPhoto?ruleForm.yonghuPhoto:''"
                         @change="yonghuPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'"  label='' prop="zhuxiaoTypes">
                 <el-select v-model="ruleForm.zhuxiaoTypes" disabled  placeholder=''>
                     <el-option
                             v-for="(item,index) in zhuxiaoTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuEmail">
               <el-input v-model="ruleForm.yonghuEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='shenherenyuan'"  label='' prop="shenherenyuanUuidNumber">
               <el-input v-model="ruleForm.shenherenyuanUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='shenherenyuan'"  label='' prop="shenherenyuanName">
               <el-input v-model="ruleForm.shenherenyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='shenherenyuan'"  label='' prop="shenherenyuanPhone">
               <el-input v-model="ruleForm.shenherenyuanPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='shenherenyuan'"  label='' prop="shenherenyuanIdNumber">
               <el-input v-model="ruleForm.shenherenyuanIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='shenherenyuan'" label='' prop="shenherenyuanPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.shenherenyuanPhoto?ruleForm.shenherenyuanPhoto:''"
                         @change="shenherenyuanPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='shenherenyuan'"  label='' prop="shenherenyuanEmail">
               <el-input v-model="ruleForm.shenherenyuanEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yewurenyuan'"  label='' prop="yewurenyuanUuidNumber">
               <el-input v-model="ruleForm.yewurenyuanUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yewurenyuan'"  label='' prop="yewurenyuanName">
               <el-input v-model="ruleForm.yewurenyuanName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yewurenyuan'"  label='' prop="yewurenyuanPhone">
               <el-input v-model="ruleForm.yewurenyuanPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yewurenyuan'"  label='' prop="yewurenyuanIdNumber">
               <el-input v-model="ruleForm.yewurenyuanIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yewurenyuan'" label='' prop="yewurenyuanPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.yewurenyuanPhoto?ruleForm.yewurenyuanPhoto:''"
                         @change="yewurenyuanPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='yewurenyuan'"  label='' prop="yewurenyuanEmail">
               <el-input v-model="ruleForm.yewurenyuanEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-form-item v-if="flag=='users'" label="" prop="username">
             <el-input v-model="ruleForm.username"
                       placeholder=""></el-input>
         </el-form-item>
         <el-col :span="12">
             <el-form-item v-if="flag!='users'"  label="" prop="sexTypes">
                 <el-select v-model="ruleForm.sexTypes" placeholder="">
                     <el-option
                             v-for="(item,index) in sexTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item>
                 <el-button type="primary" @click="onUpdateHandler"> </el-button>
             </el-form-item>
         </el-col>
     </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
        ruleForm: {},
        flag: '',
        usersFlag: false,
        // sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
        // 
        zhuxiaoTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
    };
  },
  mounted() {
    //
    var table = this.$storage.get("sessionTable");
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    if (this.role != ""){
    }

    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
//  
//  
//  
      } else {
        this.$message.error(data.msg);
      }
    });

//   
//   
//   

      this.$http({
          url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=sex_types`,
          method: "get"
      }).then(({ data }) => {
          if (data && data.code === 0) {
              this.sexTypesOptions = data.data.list;
          } else {
              this.$message.error(data.msg);
          }
      });
   this.$http({
       url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=zhuxiao_types`,
       method: "get"
   }).then(({ data }) => {
       if (data && data.code === 0) {
          this.zhuxiaoTypesOptions = data.data.list;
      } else {
          this.$message.error(data.msg);
      }
    });
  },
  methods: {
    chongzhi() {
      this.$router.replace({ path: "/pay" });
    },
    yonghuPhotoUploadChange(fileUrls) {
        this.ruleForm.yonghuPhoto = fileUrls;
    },
    shenherenyuanPhotoUploadChange(fileUrls) {
        this.ruleForm.shenherenyuanPhoto = fileUrls;
    },
    yewurenyuanPhotoUploadChange(fileUrls) {
        this.ruleForm.yewurenyuanPhoto = fileUrls;
    },


    onUpdateHandler() {
                         if((!this.ruleForm.yonghuUuidNumber)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuName)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yonghu' ==this.flag && this.ruleForm.yonghuPhone&&(!isMobile(this.ruleForm.yonghuPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.yonghuIdNumber)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuPhoto)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.zhuxiaoTypes)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yonghu' ==this.flag && this.ruleForm.yonghuEmail&&(!isEmail(this.ruleForm.yonghuEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.shenherenyuanUuidNumber)&& 'shenherenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.shenherenyuanName)&& 'shenherenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'shenherenyuan' ==this.flag && this.ruleForm.shenherenyuanPhone&&(!isMobile(this.ruleForm.shenherenyuanPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.shenherenyuanIdNumber)&& 'shenherenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.shenherenyuanPhoto)&& 'shenherenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'shenherenyuan' ==this.flag && this.ruleForm.shenherenyuanEmail&&(!isEmail(this.ruleForm.shenherenyuanEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.yewurenyuanUuidNumber)&& 'yewurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yewurenyuanName)&& 'yewurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yewurenyuan' ==this.flag && this.ruleForm.yewurenyuanPhone&&(!isMobile(this.ruleForm.yewurenyuanPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.yewurenyuanIdNumber)&& 'yewurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yewurenyuanPhoto)&& 'yewurenyuan'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yewurenyuan' ==this.flag && this.ruleForm.yewurenyuanEmail&&(!isEmail(this.ruleForm.yewurenyuanEmail))){
                                 this.$message.error(``);
                                 return
                             }
        if((!this.ruleForm.sexTypes) && this.flag!='users'){
            this.$message.error('');
            return
        }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
        this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
